import "./projects.scss";
import SocialNet from "../../images/social-net.png";
import productsShop from "../../images/cosmetic-web.png";
import portfolio from "../../images/portfolio.png";
import talWeb from "../../images/talWeb.png";
import muonlineweb from "../../images/muonlineweb.png";
import Soon from "../../images/soon.png";

const Projects = () => {
  const projectLinks = {
    socialNet: "https://phpstack-737101-2470087.cloudwaysapps.com",
    muonline: "https://phpstack-737101-2634760.cloudwaysapps.com",
    productsShop: "https://products-shopp.netlify.app",
    portfolio: "https://roi-lopez-portfolio.netlify.app",
    talWeb: "https://tal-lopez.co.il",
  };

  function toProject(project) {
    if (project === "socialNet") {
      return window.open(`${projectLinks.socialNet}`, "_blank");
    }

    if (project === "muonline") {
      return window.open(`${projectLinks.muonline}`, "_blank");
    }

    if (project === "productsShop") {
      return window.open(`${projectLinks.productsShop}`, "_blank");
    }

    if (project === "portfolio") {
      return window.open(`${projectLinks.portfolio}`, "_blank");
    }

    if (project === "talWeb") {
      return window.open(`${projectLinks.talWeb}`, "_blank");
    }
  }

  return (
    <div className="container container-project  d-flex justify-content-center d-flex flex-column justify-content-center align-items-center ">
      <div id="projects"></div>

      <div className="row d-flex justify-content-center">
        <div className="col-12 text-center mb-5 pb-2">
          <h1>PROJECTS</h1>
          <div className="bottom-line"></div>
          <span>Here Are Some of My Projects</span>
        </div>

        {projects.map((project) => {
          return (
            <div className={project.containerClass}>
              <div className="pt-2" onClick={() => toProject(project.name)}>
                <img
                  className="card-img-top "
                  style={{ borderRadius: "10px" }}
                  src={project.src}
                  alt={project.alt}
                />
              </div>

              <div className="card-body">
                <div className="float-end">
                  <a
                    style={{
                      textDecoration: "none",
                      color: "white",
                      fontSize: "20px",
                    }}
                    href={project.git}
                    target="_blank"
                  >
                    {project.git && (
                      <i className="bi bi-github text-danger"></i>
                    )}
                  </a>
                </div>
                <h5 className="card-title">{project.cardTitle}</h5>
                <p className="card-text pb-3">
                  <span>{project.crud}</span>

                  <div>
                    {project.technologies.map((tec) => (
                      <span className="infoProjectSkills">{tec}</span>
                    ))}
                  </div>
                </p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

const projects = [
  {
    containerClass: "col-12 col-xl-5 col-lg-6 card containerProjectImage",
    name: "socialNet",
    src: SocialNet,
    git: "https://github.com/roilopezz/social-network-frontend",
    cardTitle: "Social Network",
    crud: "Login , Post , Like , Add Comment , Upload Photo , Delete",
    technologies: [
      "React",
      "Context",
      "Node.js",
      "bootstrap",
      "Axios",
      "Joi",
      "Material-ui",
      "bcrypt",
      "express",
      "multer",
      "socket.io",
      "MongoDB",
      "Sass",
    ],
    alt: "socialNet",
  },

  {
    containerClass:
      "col-12 col-xl-5 col-lg-6 card containerProjectImage ms-xl-2",
    name: "muonline",
    src: muonlineweb,
    git: "",
    cardTitle: "MuOnline Website for Game",
    crud: "Login , Register , Player Options for Game",
    technologies: [
      "React",
      "Redux",
      "Node.js",
      "Mui",
      "express",
      "Jwt",
      "SqlServer",
      "Axios",
      "Joi",
      "Sass",
    ],
    alt: "muonline",
  },

  {
    containerClass: "col-12 col-xl-5 col-lg-6 card containerProjectImage ",
    name: "productsShop",
    src: productsShop,
    git: "https://github.com/roilopezz/Products-Shop",
    cardTitle: "Products Shop",
    crud: "Shop app , Add , Delete",
    technologies: ["React", "bootstrap", "LocalStorage"],
    alt: "productsShop",
  },
  {
    containerClass:
      "col-12 col-xl-5 col-lg-6  card containerProjectImage ms-xl-2",
    name: "portfolio",
    src: portfolio,
    git: "",
    cardTitle: "Portfolio",
    crud: "My Portfolio",
    technologies: ["React", "bootstrap"],
    alt: "portfolio",
  },
  {
    containerClass: "col-12 col-xl-5 col-lg-6 card containerProjectImage ",
    name: "talWeb",
    src: talWeb,
    git: "",
    cardTitle: "Cosmetic Website",
    crud: "Landing Page + form",
    technologies: [
      "React",
      "Node.js",
      "Express",
      "Googleapis",
      "Nodemailer",
      "Axios",
      "Mui",
    ],
    alt: "Cosmetic Website",
  },
  {
    containerClass:
      "col-12 col-xl-5 col-lg-6 card containerProjectImage ms-xl-2",
    name: "soon",
    src: Soon,
    git: "",
    cardTitle: "COMING SOON..",
    crud: "COMING SOON..",
    technologies: ["COMING SOON.."],
    alt: "COMING SOON..",
  },
];

export default Projects;
