import "./about.scss";

const About = () => {
  return (
    <div className="container container-about">
      <div id="about"></div>
      <div className="row">
        <div className="col-12 text-center  pb-5">
          <h1>ABOUT ME</h1>
          <div className="bottom-line"></div>
        </div>
        <div className="col-12 col-xl-5 ">
          I discovered the great passion for coding as a child, and didn't stop
          ever since! I planned, designed and developed major and minor web
          projects, using variety of programming languages. These days I'm
          working on new projects and learning new skills every day. Looking for
          a job FrontEnd - BackEnd - FullStack
        </div>
        <div className="col-0 col-xl-1"></div>
        <div className="col-12 col-xl-4 mt-4 mt-xl-0 containerSkills">
          {skills[0].map((skill) => {
            return (
              <>
                <div>{skill.title}</div>
                <div className="subContainerSkills">
                  {skill.technologies.map((s) => {
                    return <span className="skills">{s}</span>;
                  })}
                </div>
              </>
            );
          })}

          {skills[1].map((skill) => {
            return (
              <>
                <div className="mt-4">{skill.title}</div>
                <div className="subContainerSkills">
                  {skill.technologies.map((s) => {
                    return <span className="skills">{s}</span>;
                  })}
                </div>
              </>
            );
          })}
        </div>
      </div>
    </div>
  );
};

const skills = [
  [
    {
      name: "topSkills",
      title: "My Top Skills :",
      technologies: [
        "JavaScript",
        "HTML",
        "SASS",
        "React",
        "Redux",
        "Node.js",
        "Express",
        "MongoDB",
      ],
    },
  ],
  [
    {
      title: "Other Skills : ",
      name: "anotherSkills",
      technologies: ["Angular", "CSS", "MySql", "SqlServer"],
    },
  ],
];

export default About;
